.buttons-prompt {

  position: relative;
  margin-top: 10px;

  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;



  &__button {
    border-radius: 5px;
    box-shadow: 0px 0px 1px 1px rgb(0, 0, 0, 0.05);

    @media (max-width: 767px) {
      font-size: 20px;
      flex: 1 1 100%;
      margin-bottom: 5px;
    }

    @media (min-width: 767px) {
      &:not(:last-child) {
        margin-right: 10px;
      }
    }

    box-shadow: 0px 0px 1px 1px rgb(0, 0, 0, 0.05);
    border: none;
    cursor: pointer;
    text-align: center;
    outline: none;
    transition: all 0.1s ease 0s;
    position: relative;

    &:disabled {
      cursor: default;
      transition: all 0s ease 0s;
      color: #acacac;
    }

    width: 100%;
    flex: 1 1 49%;

    color: black;
    font-size: 24px;
    background: rgba(0, 0, 0, 0.1);

    padding: 10px 0;

  }
}